.shopItem{padding:5px 15px;background:linear-gradient(45deg, #ff00cc, #3333ff);border-radius:15px;font-size:20px;color:#fff;animation:gradientAnimation 5s ease infinite alternate;cursor:pointer}.shopItem__title{font-size:18px;text-align:center;color:#f90;text-shadow:2px 2px 4px rgba(0,0,0,.5)}.shopItem--bought{background:#888;position:relative;animation:none;cursor:default}.shopItem--bought::after{position:absolute;content:"КУПЛЕНО";right:0;bottom:0;left:50%;color:#fff;background-color:red;font-weight:bold;transform:rotate(10deg) translateY(-10px) translateX(-50%);text-align:center;width:70%;height:30px;font-size:20px;text-transform:uppercase;padding:5px;box-shadow:0 0 5px rgba(0,0,0,.5)}.shopItem--bought .shopItem__inner{filter:blur(1px)}@keyframes gradientAnimation{0%{background-position:0% 50%}100%{background-position:100% 50%}}